.sidebar-wrapper {
  width: 300px;
  display: flex;
  height: 100%;
  background: #20283a;
  flex-direction: column;
  box-shadow: 2px 0px 20px #00000030;
  position: relative;
}
.sidebar-wrapper header {
  height: 40px;
  min-height: 40px;
  max-height: 40px;
  background: #181e2b;
  padding-right: 5px;
  margin: 6px 4px;
  border-radius: 0px;
}
.sidebar-wrapper header .watchlist {
  padding: 10px;
  color: #949fab;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 500;
}
.sidebar-wrapper header .search {
  border: none;
  background: #353c4f;
  border-radius: 2px;
  padding: 5px;
  color: #f4f6f9;
  width: 83px;
  outline: none;
  height: auto;
  font-size: 13px;
}
.sidebar-wrapper nav {
  margin: 0 4px;
}
.sidebar-wrapper #watching-list {
  list-style-type: none;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #33394a;
  padding: 0;
  padding-bottom: 5px;
}

.sidebar-wrapper #stock-list-display {
  list-style: none !important;
  margin: 0 !important;
  width: 100%;
}

.sidebar-wrapper #watching-list-item {
  width: 100%;
  display: flex;
  justify-content: space-between !important;
  align-items: center;
  padding: 0;
  cursor: pointer;
  position: relative;
}

.sidebar-wrapper #watching-list-item:hover {
  background: #171b29;
}

