.carousel,
.carousel-inner,
.carousel-item,
.carousel img {
  height: 100%;
}
.carousel-item img {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.carousel-indicators {
  margin: 0;
  padding-left: 30px;
  bottom: 50px;
  justify-content: flex-start;
}
.carousel-indicators li {
  width: 23px;
  height: 7px;
  border-radius: 4px;
  background: #fff;
  border: 0;
  opacity: 1;
}
.carousel-indicators li.active {
  width: 44px;
  background: #4786ff;
}
.overlay {
  position: absolute;
  height: 150px;
  bottom: 0;
  right: 15px;
  left: 0;
  padding: 30px;
  border-bottom-right-radius: 5px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 1) 100%
  );
}

@media screen and (max-width: 768px) {
  .carousel,
  .overlay {
    display: none;
  }
}
